.grayBg {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: #F3F3F3;
}

.paragraph {
  color: $black;
  font-size: 16px;
  line-height: 30px;
  height: 65%;
  top: 27%;

  @include media('>phone') {
    top: 22%;
  }

  @include media('>tablet') {
    font-size: 28px;
    line-height: 37px;
    top: 35%;
  }
}

.ourProductsArt {
  position: absolute;
  bottom: 0;
  width: 100%;
}
