@import "/src/media-query-mixins";
@import "/src/dimentions";

.cover {
  z-index: 1;
  width: 100vw;
  height: 100%;
  display: flex;
  flex-direction: column;

  .header {
    position: absolute;
    top: 20%;
    left: $gutter-phone;
    width: 66%;
    z-index: 1;

    @include media('>tablet') {
      left: $gutter-desktop;
    }

    @include media('>desktop') {
      left: $gutter-wide-screen;
      width: 55%;
    }
  }

  .art {
    height: 50%;
    bottom: 0;
    right: 0;
    position: absolute;

    @include media('>tablet') {
      height: 70%;
      bottom: 8%;
    }

  }

  .footer {
    height: 15%;
    width: 100%;
    position: absolute;
    bottom: 0;
    z-index: 2;
    display: flex;
    //padding: 0 $gutter-phone;
    justify-content: space-around;
    align-items: center;
    box-sizing: border-box;

    @include media('>phone') {
      height: 127px;
      //padding: 0 $gutter-tablet;
    }

    @include media('>tablet') {
      //padding: 0 100px;
      left: 35%;
      right: 10%;
      width: auto;
    }

    @include media('>desktop') {
      font-size: 91px;
    }
  }

  .text {
    //width: 240px;
    text-align: left;
    line-height: 1.5em;
    font-size: 16px;
    transform: translateY(1px);
  }

  .title {
    color: white;
    width: 100%;
  }

  .date {
    width: 100%;
    color: white;
    opacity: 0.6;
  }

  .arrow {
    //position: absolute;
    //top: 50%;
    //right: $gutter-phone;
    color: white;
    height: 46px;
    position: relative;

    //@include media('>phone') {
    //  right: $gutter-tablet;
    //}
    //
    //@include media('>tablet') {
    //  right: 100px;
    //}
  }
}

.address {
  color: white;
  display: flex;
  align-items: center;

  svg {
    width: 20px;
    margin-right: 5px;
  }
}

.addressText {
  height: 14px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: calc(100% - 25px);
}
